.sliding-switch {
  height: 27px;
  position: relative;
}
.sliding-switch .switch {
    display: inline-block;
    height: 28px;
    position: relative;
    width: 60px;
}
.sliding-switch .switch input {
      display: none;
}
.sliding-switch .labels span {
    color: #999;
    font-size: 11px;
    pointer-events: none;
    position: absolute;
    top: 6px;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}
.sliding-switch .labels span.white {
      color: #fff;
}
.sliding-switch .labels .label-left {
    left: 11px;
}
.sliding-switch .labels .label-right {
    left: 37px;
}
.sliding-switch .slider {
    background-color: #f2f4fa;
    bottom: 0;
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    -webkit-transition: .4s;
    transition: .4s;
}
.sliding-switch .slider::before {
      background-color: #05c0a5;
      bottom: 3px;
      content: '';
      height: 22px;
      left: 4px;
      padding: 1px 7px;
      position: absolute;
      -webkit-transition: .4s;
      transition: .4s;
      width: 25px;
}
.sliding-switch .slider.round {
      border-radius: 34px;
}
.sliding-switch .slider.round::before {
      border-radius: 100%;
}
.sliding-switch input:checked + .slider {
    background-color: #f2f4fa;
}
.sliding-switch input:focus + .slider {
    -webkit-box-shadow: 0 0 1px #f2f4fa;
            box-shadow: 0 0 1px #f2f4fa;
}
.sliding-switch input:checked + .slider::before {
    -webkit-transform: translateX(26px);
            transform: translateX(26px);
}

